@use '../base/mixins' as m;
@use '../base/variables' as v;

.productos {
    &__heading {
        @include m.headingCursiva;
        margin-bottom: 5rem;

    }
    &__grid {
        @include m.grid(1, 2rem);
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}

.producto {
    &__contenido {
        background-color: v.$blanco;
        padding: 2rem;

    }
    &__nombre {
        @include m.heading;
        font-size: 2.4rem;
        color: v.$secundario;
    }
    &__descripcion {
        font-size: 1.6rem;
        text-transform: uppercase;
        line-height: 1.8;
    }
    &__precio {
        text-align: center;
        color: v.$gris;
        font-size: 2.8rem;
        margin: 0;
    }
    &__imagen {
        overflow: hidden;
        img {
            width: 100%;
            object-fit: cover;
            transition: transform .3s ease;
            &:hover {
                transform: scale(1.1);
            }
        }
    }
}